@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__oc {
  display: flex;
  margin: 120px 120px 0 120px;
}
.astudio__oc-block1 h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
  margin-bottom: 17px;
  margin-top: 16px;
}
.astudio__oc-block1 p {
  font-family: "Averta Demo PE Cutted Demo";
  font-weight: 400;
  font-family: 18px;
  line-height: 28px;
  color: #757575;
  margin: 17px 60px 0 0;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__oc-block2 {
    width: 30%;
  }
}
.astudio__oc .ourclient-images {
  display: flex;
  align-items: center;
  padding-top: 75px;
}
.astudio__oc .ourclient-images .ourclient-img {
  margin-left: 60px;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__oc .ourclient-images .ourclient-img {
    margin-top: 30px;
    margin-bottom: 12px;
  }
}
.astudio__oc .ourclient-images .ourclient-img:last-child {
  padding-top: 10px;
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__oc .ourclient-images {
    flex-direction: column;
    padding-top: 0;
  }
}
@media (min-width: 0) and (max-width: 768px) {
  .astudio__oc {
    flex-direction: column;
    margin-left: 14px;
    margin-right: 0;
    margin-top: 30px;
  }
}